











import Vue from 'vue';
import Component from 'vue-class-component';

@Component({
  props: [
    'title',
    'icon',
    'message',
    'type',
  ],
})
export default class AlertCard extends Vue {
  public type: string;

  public typeList: { [key: string]: string } = {
    success: 'fa-check',
    error: 'fa-times',
  };

  public get typeClass(): string {
    return this.typeList[this.type] || 'fa-times';
  }
}



